*{box-sizing:border-box}html{color:#354a5f;font-family:sans-serif;font-size:1rem;line-height:1.4;font-size:14px}body{margin:0}.app{display:flex;flex-direction:row;width:100%;max-width:80rem;margin:0 auto;box-sizing:border-box}.sidebar{width:20rem;height:100vh;display:flex;flex-direction:column;border-right:1px solid rgba(0,0,0,0.07)}.sidebar__header{border-bottom:1px solid rgba(0,0,0,0.07);padding:0.5rem}.sidebar__search-input{color:#354a5f;font-family:sans-serif;font-size:1rem;line-height:1.4;width:100%;border:none;padding:0.5rem;border:1px solid transparent;border-radius:0}.sidebar__search-input:focus{box-shadow:0 0 5px #467fcf;border:1px solid #467fcf;outline:none}.sidebar__list{list-style:none;padding:0;margin:0 0 0 1rem;overflow-y:auto;width:100%}.sidebar__list--root{margin:0}.sidebar__item{display:flex;flex-wrap:wrap}.sidebar__item .sidebar__list{display:none}.sidebar__item--open .sidebar__list{display:block}.sidebar__item-opener{width:2.5rem;height:2.5rem;display:flex;justify-content:center;align-items:center;cursor:pointer}.sidebar__link{display:flex;min-height:2.5rem;align-items:center;padding:0.5rem 1rem;text-decoration:none;color:#354a5f;flex-grow:1}.sidebar__link:hover{text-decoration:underline}.sidebar__link.active{background-color:#e8eff9}.content{height:100vh;overflow-y:auto;flex-grow:1;margin-left:1rem}.page{width:50rem}.page a,.page a:visited{color:#467fcf;text-decoration:none}.page a:hover{text-decoration:underline}.page a.external,.page a.external:visited{color:#6e54ba}.page a.missing{color:#d32f2f}.page a.current{font-weight:bold}.page li p{margin:0}.page blockquote{border-left:0.25rem solid rgba(0,0,0,0.07);padding-left:0.5rem;margin:0}.reference{border-bottom:1px solid rgba(0,0,0,0.07);padding:1rem 0}.reference h3{margin-top:0}.bookmark{margin-bottom:1rem}.bookmark__title{margin:0}.bookmark__url{font-size:0.875rem;color:#778592}.bookmark__tags{margin:0.5rem 0}.bookmark__tags a{margin-right:0.5rem}.bookmark__description p{margin:0 0 0.5rem 0}
